import Vue from 'vue'
import Router from 'vue-router'
import LifeTheSorting from "../pages/LifeTheSorting"

Vue.use(Router)

export default new Router({
  routes: [

    {
      path: '/',
      name: 'LifeTheSorting',
      component: LifeTheSorting
    },

  ]
})



