        </li>
        <li><span>折扣</span><span class="list-ico"><i class="iconfont1"  @click="orderFn('ListDiscount', false)">∧</i><i class="iconfont2" @click="orderFn('ListDiscount', true)">∨</i></span>
        </li>
        <li><span>品牌</span><i class="iconfont icon-mark-o"></i></li>
        <li><span>筛选</span><i class="iconfont icon-screen"></i></li>
