            <p class="list-pro-price"><span class="list-pro-rob">{{p.ListProRob}}</span>￥{{p.ListProPrice}}<small><span>
                    ￥  {{p.ListProSmall}}</span> {{p.ListDiscount}}折</small>
            </p>
            <p class="list-pro-name">
              {{p.ListBrand}} |
              {{p.ListProName}}
            </p>
        </div>
      </div>
    </section>
  </div>
</template>

<script>
    import LifeTheSortingcss from "../assets/css/LifeTheSorting.css"
    export default {
        name: "LifeTheSorting",
      components:{LifeTheSortingcss},
      data(){
          return{
            "letter": "",
            "original":false ,
            "products":[{
